// variables
$break-big: 2000px;
$break-mid: 1440px;
$break-small: 1200px;
$break-tablet: 800px;
$break-smartphone: 500px;
$break-small-smartphone: 400px;

// end of variables

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eeeeee;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

.container-loader{
    text-align: center;
    align-items: center;
    .pointer{
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 10%;
        transform: translate(-50%, -50%);
        width: 300px;
        height: 300px;
        background-color: #fff;
        border-radius: 50%;
        transition: 0.000s ease-in;
        z-index: 6;
        mix-blend-mode: difference;
    }
    .thumb{
       background-color: #eeeeee;
        .container-post{
            justify-content: center;
            align-content: center;
            display: inline-grid;
            .main-cont{
                padding-top: 1em;
            }
            .inline-class{
                display: inline-flex;
                justify-content: center;
                align-items: center;
            }
            .left-side{
                text-align: left;
                margin: 1em;
            }
            .charts-container{
                margin: 1em;
            }
            .title{
                font-size: 42px;
                margin: 1em;
                height: 30%;
                width: 100%;
                @media screen and (max-width: $break-small-smartphone) {
                    font-size: 32px;
                }
            }
            .container-title{
                height: 100px;
                .title{
                    font-size: 42px;
                    margin: 1em;
                    height: 30%;
                    width: 100%;
                }
            }
            .container-info-main{
                display: inline-flex;
                justify-content: center;
                .container-info{
                    height: auto;
                    width: 600px;
                }
            }
        }
    }
}

.link-to{
    padding: 0 ;
    color: #424242;

}
.col-main{
    text-align: center;
    &:hover {
        .span-name-main{
            color: rgb(255, 0, 0);
        }
    }
}
.menu-div{
    height: 65px;
    .text-to-small-smartphone{
        display: none;
    }
    @media screen and (max-width: $break-small-smartphone) {
        background-color: transparent;
        .text-to-small-smartphone{
            display: block;
            justify-content: center;
            display: flex;
            font-size: 42px;
            font-weight: 600;
            margin-top: 2em;
        }
        nav.navbar{
            display: none;
        }
    }
}
.container-main-own{
    @media screen and (max-width: $break-big) {
        width: 1200px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        align-self: center;
        .row-main-own{
            display: inline-flex;
            justify-content: center;
            width: inherit;
        }
    }
    @media screen and (max-width: $break-small-smartphone) {
        display: grid;
        width: 100%;
        .row-main-own{
            display: grid;
            width: 380px;
            padding: 5em;
        }
    }
}
.content-main-own{
    margin: 1em;
    padding: 3em;
    display: flex;
    justify-content: center;
    height: 100vh;
    @media screen and (max-width: $break-small-smartphone) {
        padding: 2em;
        margin: 0;
        height: 100%;
    }
}

.container-home{
    width: 100%;
}

.col-home{
    width: 30%;
    margin: 2em;
}

.footer-container{
    text-align: center;
    padding: 2em 0 2em 0;
    width: auto;
    color: darkred;
}

.loading{
    display: flex;
    justify-content: center;
}

.loader{
    font-size: 42px;
    padding: 3rem;
}